.dots-container {
  background-color: #000;
  overflow: hidden;
  height: 100vh;

  #paperCanvas {
    position: relative;
    width: 100vw;
    height: 100vh;
  }
  
  .options {
    padding: 0px;
    margin: 0px;
    margin-top: 20px;
    color: #999;
    list-style: none;
    display: flex;
    gap: 40px;
    justify-content: center;


    @media (max-width: 500px) {
      padding-top: 100px;
    }
  }
  
  li {
    span {
      display: inline-block;
      padding: 20px;
      cursor: pointer;

      &:active, &:hover {
          color: #fff;
      }
    }
  }
}