.entanglement-container {
  background-color: #000;

  #paperCanvas {
    position: relative;
    width: 100vw;
    height: 100vh;
    top: 0;
  }
  
  h3 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 2.5vw;
    color: #fff;
    z-index: 0;
    mix-blend-mode: difference;

    @media (max-width: 500px) {
      font-size: 48px;
      padding: 25px;
      width: 100%;
    }
  }
  
  .options {
    padding: 0px;
    margin: 0px;
    color: #999;
    list-style: none;
    display: flex;
    justify-content: center;
    gap: 40px;
    -webkit-user-select: none; /* Safari */
    -ms-user-select: none; /* IE 10 and IE 11 */
    user-select: none; /* Standard syntax */

    @media (max-width: 500px) {
      padding-top: 100px;
    }
  }
  
  .options li span {
    display: inline-block;
    padding: 20px;
    cursor: pointer;
  }
  
  .options li span.active, .options li span:hover {
    color: #fff;
  }
}